}

.carousel-slides {

    height: 100%;
    position: relative;
    transition: -webkit-filter 500ms ease-out;
    transition: filter 500ms ease-out;
    transition: filter 500ms ease-out, -webkit-filter 500ms ease-out;

}

.carousel-slide {

    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    bottom: 0;

}

.carousel-content-container {

    position: relative;
    left: 0;
